.square-holder {
	padding: 30px;
	border: 1px solid #fdd8d8;
	align-items: center;
	display: flex;
	align-items: center;
	justify-content: center;
	margin-bottom: 20px;
	background-color: #ffffff;
	min-height: 200px;
}

.square-holder img {
	max-width: 100%;
	/* filter: grayscale(100%);
